/* PageComponent.css */

.page-container {
    background-color: rgb(86, 103, 75);
    padding-top: 4vw;
    color: rgb(228, 226, 212);
    display: flex;
    flex-direction: column;
    
    
    @media (min-width: 768px) {
      flex-direction: row;
    }
  }

  
  h1{
    font-size: 5.5vw;
  }
  
  p{
    font-size: 2vw;
  }
  .text-container {
    
    padding: 20px;
    text-align: start;

    @media (min-width: 768px) {
      flex: 1;
      padding: 40px;
    
      
    }
  
    

  }
  
  .image-container {
    @media (min-width: 768px) {
        padding-right: 10vw;
      flex: 1;
    }
  
    img {
      max-width: 100%;
      height: auto;
    }
  }
  